:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.photo-card {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo-link {
  text-decoration: none;
  display: block;
}

.photo-link:hover {
  cursor: zoom-in;
}

.info {
  justify-content: center;
  gap: 16px;
  padding-bottom: 4px;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  margin: 0;
  font-size: 12px;
  display: inline-flex;
}

